<template>
  <div>
    <h1>Management Division</h1>

    <p class="management-intro">
      The final division, Business, supports all aspects of team management. This includes marketing, sponsorship, and
      finance.We are student-backed and solar-powered.
    </p>

    
    <div id="carouselExampleInterval" class="carousel slide" data-ride="carousel">
      <ol class="carousel-indicators">
        <li data-target="#carouselExampleInterval" data-slide-to="0" class="active"></li>
        <li data-target="#carouselExampleInterval" data-slide-to="1"></li>
        <li data-target="#carouselExampleInterval" data-slide-to="2"></li>
      </ol>
      <div class="carousel-inner">
        <div class="carousel-item active" data-interval="2000">
          <img src="../../../assets/team/slides/management-slide1.jpg" class="d-block w-100" alt="...">
        </div>
        <div class="carousel-item" data-interval="2000">
          <img src="../../../assets/team/slides/management-slide2.jpg" class="d-block w-100" alt="...">
        </div>
        <div class="carousel-item" data-interval="2000">
          <img src="../../../assets/team/slides/management-slide3.jpg" class="d-block w-100" alt="...">
        </div>
      </div>
    </div>

    <div class="btns">
      <button @click='showBusiness'>
        <img src="../../../assets/team/business-icon.png" alt="" class="icon">
        Business</button>
      <button @click='showExcutive'>
        <img src="../../../assets/team/executive-icon.png" alt="" class="icon">
        Excutive</button>
    </div>

    <transition appear mode="out-in" enter-active-class=" animate__animated animate__fadeIn animate__faster"
      leave-active-class=" animate__animated animate__fadeOut animate__faster ">
      <component :is="selectedComponent"></component>
    </transition>
  </div>
</template>

<script>
  import Business from './Business.vue';
  import Excutive from './Excutive.vue';

  export default {
    data() {
      return {
        selectedComponent: 'business-section'
      }
    },
    methods: {
      showBusiness() {
        this.selectedComponent = 'business-section';
      },
      showExcutive() {
        this.selectedComponent = 'excutive-section';
      }
    },
    components: {
      'business-section': Business,
      'excutive-section': Excutive
    },

     created: function () {
      document.querySelector('#app').style.height = '400%';
    }
  }

</script>

<style scoped>
  .management-intro {
    color: #2c3e50;
    padding: 0 7vw;
    font-size: 1.3rem;
    line-height: 2;
    margin: 6vh 0;
  }

  .btns {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin: 6vh 0;
  }

  button {
    font-family: 'Ubuntu', sans-serif;
    color: #2c3e50;
    font-weight: 700;
    font-size: 1rem;
    margin: 3vh 2vw;
    border: 0;
    outline: none;
    width: 170px;
    height: 50px;
    border-radius: 10px;
    background-color: #f3c623;
    cursor: pointer;
    transition-duration: .3s;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }

  button:hover {
    transform: scale(1.1);
  }

  .icon {
    height: 40px;
    width: 40px;
  }

  @media screen and (max-width: 700px) {

  }

</style>



