<template>
  <div class="vehicles-container">
    <img src="../assets/vehicles/all-cars.jpg" class="all-cars">
    <h1 style="margin: 5vh 0;">Gallery</h1>
    <div class="carousel">

      <div class="pic-container">

        <img src="../assets/vehicles/raven.jpg" class="card" 
             :class="{mid: currentCard == 1, left: currentCard > 1}">

        <img src="../assets/vehicles/daybreak.jpg" class='card'
          :class="{mid: currentCard == 2, left: currentCard > 2, right: currentCard < 2}">

        <div class="carousel-control">
          <img src="../assets/vehicles/arrow.png" class="previous" @click="previousCard()">
          <h2>{{cars[currentCard-1]}}</h2>
          <img src="../assets/vehicles/arrow.png" class="next" @click='nextCard()'>
        </div>


      </div>

      <div class="car-info-container">

        <transition mode="out-in" enter-active-class=" animate__animated animate__fadeIn animate__faster"
          leave-active-class=" animate__animated animate__fadeOut animate__faster ">

          <div class="car-info" key="daybreak" v-if='currentCard == 2'>
            <img src="../assets/vehicles/daybreak-as1.jpg" class="car-pic">
            <img src="../assets/vehicles/daybreak-as2.jpg" class="car-pic">
            <p class="project-info">
              At the beginning of the 2017 school year, UBC Solar was presented with a new team, a new car design, and
              new
              ambitions for our team. Daybreak was designed from scratch and taught us so much about sustainable
              automotive technology. It features a 5.1kWh 18650 Lithium Ion battery, 1kW solar array, and 3.5kW NGM
              motor.
              After two years of design and manufacturing, Daybreak hit the road on June 23rd, 2019 and will be
              competing
              this summer in the 2020 American Solar Challenge.
            </p>
          </div>

          <div class="car-info" key='raven' v-else>
            <img src="../assets/vehicles/raven-1.jpg" class="car-pic">
            <img src="../assets/vehicles/raven-2.jpg" class="car-pic">
            <p class="project-info">
              Started in 2008, Raven was the team’s first solar car. Raven featured an 8kW ultra-high efficiency motor,
              1.2kW solar array, and LiFePO4 battery. The three-wheeled solar car was constructed from a chromoly steel
              chassis and double wishbone suspension. Raven was the team’s first attempt at tackling modern sustainable
              energy solutions, while setting a framework for future generations of Solar.
            </p>
          </div>

        </transition>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        currentCard: 1,
        total: 2,
        cars: ['Raven', 'DayBreak']
      }
    },

    methods: {
      previousCard() {
        if (this.currentCard - 1 >= 1) {
          this.currentCard--;
        }
      },

      nextCard() {
        if (this.currentCard + 1 <= this.total) {
          this.currentCard++;
        }
      }
    },

     created: function () {
      document.querySelector('#app').style.height = '200%';
    }

  }

</script>

<style scoped>
  .vehicles-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    color: #2c3e50;
  }

  .all-cars {
    width: 100%;
  }

  /* Carousel */
  .carousle {
    /* position: relative; */
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  img {
    border: none; /* Get rid of the default ugly border lines. */
  }

  .pic-container {
    width: 100%;
    height: 40vh;
    position: relative;
    /* top: 90vh; */
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    perspective: 1000px;
  }

  .card {
    width: 500px;
    height: 350px;
    position: absolute;
    top: 0;
    z-index: -100;
    transition: transform ease-in-out .3s, opacity ease-in-out .3s;
  }

  .carousel-control {
    position: absolute;
    top: 350px;
    /* margin: 50px 0; */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .car-pic {
    margin-top: 70px;
  }

  .card.mid {
    transform: translateX(0) rotateY(0deg);
    opacity: 1;
  }

  .card.left {
    transform: translateX(-50%) rotateY(60deg);
    opacity: 0;
  }

  .card.right {
    transform: translateX(50%) rotateY(-60deg);
    opacity: 0;
  }

  /* Car Info */
  .car-info-container {
    margin-top: 200px;
  }

  .project-info{
    padding: 0 7vw;
    line-height: 2;
    font-size: 1.5rem;
    margin-top: 12vh;
  }

  /* Arrow Icon */
  .previous,
  .next {
    height: 25px;
    width: 25px;
    margin: 0 15vw;
    transition-duration: .3s;
    -moz-transition-duration: .3s;
    cursor: pointer;
    /* position: absolute; */
  }

  .next {
    transform: rotateY(180deg);
  }

  .next:hover {
    transform: rotateY(180deg) scale(1.2);
  }

  .previous:hover {
    transform: scale(1.2);
  }

  @media screen and (max-width: 800px) {
    .all-cars {
      /* position: absolute;
      top: 0;
      height: 35vh;
      width: 100%;
      margin-bottom: 8vh; */
      display: none;
    }

    .pic-container {
      align-items: center;
    }

    .car-pic {
      transform: scale(0.7);
      /* margin-top: 150px; */
    }

    .next,
    .previous {
      margin: 0 50px;
    }

    .car-info-container {
      margin-top: 4vh;
    }

  }

</style>



