<template>
  <div>
    <h1>Electrical Division</h1>

    <p class="elec-intro">
      The Electrical team designs and configures the powering systems of the car via four sub-teams: battery management
      systems, low voltage systems, power electronics, and software.
    </p>

    <div id="carouselExampleIndicators" class="carousel slide" data-ride="carousel">
      <ol class="carousel-indicators">
        <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
        <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
        <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
      </ol>
      <div class="carousel-inner">
        <div class="carousel-item active" data-interval="2000">
          <img src="../../../assets/team/slides/elec-slide1.jpg" class="d-block w-100" alt="...">
        </div>
        <div class="carousel-item" data-interval="2000">
          <img src="../../../assets/team/slides/elec-slide2.jpg" class="d-block w-100" alt="...">
        </div>
        <div class="carousel-item" data-interval="2000">
          <img src="../../../assets/team/slides/elec-slide3.jpg" class="d-block w-100" alt="...">
        </div>
      </div>
    </div>

    <div class="btns">
      <button @click="showBMS">
        <img src="../../../assets/team/battery-icon.png" alt="" class="icon">
        BMS</button>
      <button @click="showLVS($event)">
        <img src="../../../assets/team/lvs-icon.png" alt="" class="icon">
        LVS</button>
      <button @click="showPowerElec">
        <img src="../../../assets/team/electronic-icon.png" alt="" class="icon">
        Power Electronics</button>
      <button @click="showSoftware">
        <img src="../../../assets/team/software-icon.png" alt="" class="icon">
        Software</button>
    </div>

    <transition appear mode="out-in" enter-active-class=" animate__animated animate__fadeIn animate__faster"
      leave-active-class=" animate__animated animate__fadeOut animate__faster ">
      <component :is="selectedComponent"></component>
    </transition>
  </div>
</template>

<script>
  import BMS from './BMS.vue';
  import LVS from './LVS.vue';
  import PowerElec from './PowerElec.vue';
  import Software from './Softeware';

  export default {
    data() {
      return {
        selectedComponent: 'bms-section'
      }
    },

    methods: {
      showLVS(e) {
        this.selectedComponent = 'lvs-section';
      },
      showBMS() {
        this.selectedComponent = 'bms-section';
      },
      showPowerElec() {
        this.selectedComponent = 'power-elec';
      },
      showSoftware() {
        this.selectedComponent = 'software';
      },
      toggleBtn(e) {
        const isActivated = e.target.classList.contains('selected');
        if (isActivated) {
          e.target.classList.remove('se')
        }
      }

    },

    components: {
      'bms-section': BMS,
      'lvs-section': LVS,
      'power-elec': PowerElec,
      'software': Software
    }

  }

</script>

<style scoped>
  .elec-intro {
    color: #2c3e50;
    padding: 0 7vw;
    font-size: 1.3rem;
    line-height: 2;
    margin: 6vh 0;
  }

  .btns {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin: 6vh 0;
  }

  button {
    font-family: 'Ubuntu', sans-serif;
    color: #2c3e50;
    font-weight: 700;
    font-size: 1rem;
    margin: 3vh 2vw;
    border: 0;
    outline: none;
    width: 170px;
    height: 50px;
    border-radius: 10px;
    background-color: #f3c623;
    cursor: pointer;
    transition-duration: .3s;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }

  button:hover {
    transform: scale(1.1);
  }

  .icon {
    height: 40px;
    width: 40px;
  }

  @media screen and (max-width: 700px) {
    .mech-intro {
      padding: 0 20vw;
    }

    .btns {
      display: grid;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
    }

    button {
      place-self: center;
    }
  }

</style>



