<template>
  <div class="member-info">
    <div class="member-pic">
      <img src="../../../assets/team/members/lvs-lead.jpg" alt="">
      <h2>Anthony Ho</h2>
      <h2>Subteam Lead</h2>
    </div>

    <p class="subteam-info">
      The Low Voltage Systems (LVS) sub-team designs the circuits that interface with the driver and the vehicle
      controllers.The team designs and maintains the cars’ lighting systems, horn, dashboard, and data transmission
      modules to ensure smooth operation of the vehicle in any condition.
    </p>
  </div>
</template>

<script>
  export default {

  }

</script>

<style scoped>
  .member-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin: 2vh 9vw;
  }

  .member-pic {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .subteam-info {
    padding: 0 7vw;
    line-height: 2;
    font-size: 1.3rem;
    color: #2c3e50;
  }

  h2 {
    margin: 3px;
    color: #2c3e50;
  }

  @media screen and (max-width: 700px) {
    .member-info {
      flex-direction: column;
    }

    .subteam-info {
      padding: 0 10vw;
      margin-top: 4vh;
    }
  }

</style>



