//jshint esversion:6

import Home from './components/Home.vue';
import Vehicles from './components/Vehicles.vue';
import Contact from './components/Contact.vue';
import Sponsors from './components/Sponsors.vue';
import Team from './components/Team/Team.vue';

import Electrical from './components/Team/elec/Electrical.vue';
import Management from './components/Team/manage/Management.vue';
import Mechanical from './components/Team/mech/Mechanical.vue';

export const routes = [
    {path: '', component:Home, name:'home'},
    {path: '/vehicles', component: Vehicles, name: 'vehicles'},
    {path: '/sponsors', component:Sponsors, name:'sponsors'},
    {path: '/contact', component:Contact , name:'contact'},
    {path: '/team', component: Team, name: 'team', children: [
        {path: 'electrical', component: Electrical, name: 'electrical'},
        {path: 'management', component: Management, name: 'management'},
        {path: 'mechanical', component: Mechanical, name: 'mechanical'}
    ]},
    {path: '*', redirect: ''}


];


