<template>
  <div class="member-info">

    <div class="member-pics">
      <div class="captains">
        <div class="member-pic">
          <img src="../../../assets/team/members/captain.jpg" alt="">
          <h2>Pranjal Goel</h2>
          <h2>Captain</h2>
        </div>

        <div class="member-pic">
          <img src="../../../assets/team/members/co-captain.jpg" alt="">
          <h2>Alex Ezzat</h2>
          <h2>Co-Captain</h2>
        </div>
      </div>

      <div class="co-leads">
        <div class="member-pic">
          <img src="../../../assets/team/members/executive1.jpg" alt="">
          <h2>Leo Xie</h2>
          <h2>Mechanical Lead</h2>
        </div>

        <div class="member-pic">
          <img src="../../../assets/team/members/executive2.jpg" alt="">
          <h2>Nursultan Tugolbaev</h2>
          <h2>Electrical Co-Lead</h2>
        </div>

        <div class="member-pic">
          <img src="../../../assets/team/members/executive3.jpg" alt="">
          <h2>Emily Deutsch</h2>
          <h2>Electrical Co-Lead</h2>
        </div>
      </div>

    </div>

    <p class="subteam-info">
      The executive team leads the project through a captain and leads for the electrical, mechanical, and business
      divisions. They are responsible for ensuring that each arm of the team is meeting design and manufacturing goals.
      Liaisoning with faculty, staff, and industry representatives is a core responsibility of the executive team.
      Another focus they have is grant applications, team funding, and helping members learn and find their passion
      projects. They maintain team structure, provide resources, and keep members on task to ensure fluid operation.
    </p>

  </div>
</template>

<script>
  export default {

  }

</script>

<style scoped>
  .member-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin: 2vh 9vw;
  }

  .member-pics {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .captains,
  .co-leads {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: 2vh 0;
  }

  .member-pic {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .subteam-info {
    padding: 0 7vw;
    line-height: 2;
    font-size: 1.3rem;
    color: #2c3e50;
  }

  h2 {
    margin: 3px;
    color: #2c3e50;
  }

  @media screen and (max-width: 700px) {
    .member-info {
      flex-direction: column;
    }

    .subteam-info {
      padding: 0 10vw;
      margin-top: 4vh;
    }

    .member-pics {
      flex-direction: column;
    }

    .member-pic {
      transform: scale(.7, 1);
      margin: 2vh 0;
    }

    .captains,
    .co-leads {
      flex-direction: column;
    }
  }

</style>



