<template>
  <div class="mech-container">
    <h1>Mechanical Division</h1>

    <p class="mech-intro">
      The Mechanical division consists of four sub-teams: aeroshell, chassis, battery mechanics, and SSWAB (Suspension,
      Steering, Wheels & Braking). They are responsible for designing the structural and aerodynamic components of the
      car, from first sketches to fabrication.
    </p>

    <div id="carouselExampleIndicators" class="carousel slide" data-ride="carousel">
      <ol class="carousel-indicators">
        <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
        <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
        <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
      </ol>
      <div class="carousel-inner">
        <div class="carousel-item active" data-interval="2000">
          <img src="../../../assets/team/slides/mech-slide1.jpg" class="d-block w-100" alt="...">
        </div>
        <div class="carousel-item" data-interval="2000">
          <img src="../../../assets/team/slides/mech-slide2.jpg" class="d-block w-100" alt="...">
        </div>
        <div class="carousel-item" data-interval="2000">
          <img src="../../../assets/team/slides/mech-slide3.jpg" class="d-block w-100" alt="...">
        </div>
      </div>
    </div>

    <div class="btns">
      <button @click="showAeroShell">
        <img src="../../../assets/team/aeroshell-icon.png" class="icon">
        Aeroshell</button>
      <button @click="showBatteryMech">
        <img src="../../../assets/team/battery-icon.png" class="icon">
        Battery Mechanical</button>
      <button @click="showChassis">
        <img src="../../../assets/team/chassis-icon.png" class="icon">
        Chassis</button>
      <button @click="showSuspension">
        <img src="../../../assets/team/suspension-icon.png" class="icon">
        Suspension</button>
    </div>


    <transition appear mode="out-in" enter-active-class=" animate__animated animate__fadeIn animate__faster"
      leave-active-class=" animate__animated animate__fadeOut animate__faster ">
      <component :is="selectedComponent"></component>
    </transition>
  </div>
</template>

<script>
  import Aeroshell from './Aeroshell.vue';
  import BatteryMech from './BatteryMech.vue';
  import Chassis from './Chassis.vue';
  import Suspension from './Suspension.vue';

  export default {
    data() {
      return {
        selectedComponent: 'aero-shell'
      }
    },
    methods: {
      showAeroShell() {
        this.selectedComponent = 'aero-shell';
      },
      showBatteryMech() {
        this.selectedComponent = 'battery-mech';
      },
      showChassis() {
        this.selectedComponent = 'chassis';
      },
      showSuspension() {
        this.selectedComponent = 'suspension';
      }
    },

    components: {
      'aero-shell': Aeroshell,
      'battery-mech': BatteryMech,
      'chassis': Chassis,
      'suspension': Suspension
    },

    created: function () {
      document.querySelector('#app').style.height = '300%';
    }


  }

</script>

<style scoped>
  .mech-container {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    /* justify-content: center; */
  }

  .mech-intro {
    color: #2c3e50;
    padding: 0 7vw;
    font-size: 1.3rem;
    line-height: 2;
    margin: 6vh 0;
  }

  .btns {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin: 6vh 0;
  }

  button {
    font-family: 'Ubuntu', sans-serif;
    color: #2c3e50;
    font-weight: 700;
    font-size: 1rem;
    margin: 3vh 2vw;
    border: 0;
    outline: none;
    width: 170px;
    height: 50px;
    border-radius: 10px;
    background-color: #f3c623;
    cursor: pointer;
    transition-duration: .3s;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }

  button:hover {
    transform: scale(1.1);
  }

  .icon {
    height: 40px;
    width: 40px;
  }

  @media screen and (max-width: 700px) {
    .mech-intro {
      padding: 0 20vw;
    }

    .btns {
      display: grid;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
    }

    button {
      place-self: center;
    }
  }

</style>



