<template>
  <div class="member-info">

    <div class="member-pics">
      <div class="member-pic">
        <img src="../../../assets/team/members/business-lead1.jpg" alt="">
        <h2>Sage Broomfield</h2>
        <h2>Co-Subteam Lead</h2>
      </div>

      <div class="member-pic">
        <img src="../../../assets/team/members/business-lead2.jpg" alt="">
        <h2>Charlotte Lewthwaite</h2>
        <h2>Co-Subteam Lead</h2>
      </div>
    </div>

    <p class="subteam-info">
      The business team consists of members from several disciplines including international relations, business,
      economics, and engineering. The business team is responsible for the administrative, financial and marketing
      aspects of the team. This includes marketing materials, video production and social media, budgeting, sponsorship,
      fundraising, and outreach events. The business team ensures that UBC Solar’s public image emphasizes our core
      values: professionalism, smart design, and innovative thinking
    </p>

  </div>
</template>

<script>
  export default {

  }

</script>

<style scoped>
  .member-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin: 2vh 9vw;
  }

  .member-pics {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }

  .member-pic {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .subteam-info {
    padding: 0 7vw;
    line-height: 2;
    font-size: 1.3rem;
    color: #2c3e50;
  }

  h2 {
    margin: 3px;
    color: #2c3e50;
  }

  @media screen and (max-width: 700px) {
    .member-info {
      flex-direction: column;
    }

    .subteam-info {
      padding: 0 10vw;
      margin-top: 4vh;
    }

    .member-pics {
      flex-direction: column;
    }

    .member-pic {
      transform: scale(.7, 1);
      margin-top: 4vh;
    }
  }

</style>



