<template>
  <div class="member-info">

    <div class="member-pics">
      <div class="member-pic">
        <img src="../../../assets/team/members/suspension-lead1.jpg" alt="">
        <h2>Adrian Lam</h2>
        <h2>Co-Subteam Lead</h2>
      </div>

      <div class="member-pic">
        <img src="../../../assets/team/members/suspension-lead2.jpg" alt="">
        <h2>David Swewart</h2>
        <h2>Co-Subteam Lead</h2>
      </div>
    </div>

    <p class="subteam-info">
      The Suspension, Steering, Wheels, and Braking (SSWAB) team designs and builds the vehicle’s critical drive
      systems. The team performs in-depth analysis/FEA on all designs to ensure vehicle safety and strong race
      performance. Projects include 3D modeling, optimization, and analysis of the vehicle’s on-road mechanics and
      dynamics. Most components are manufactured in house, providing a strong hands-on experience while also emphasizing
      the necessities of strong mechanical theory and manufacturability.
    </p>

  </div>
</template>

<script>
  export default {

  }

</script>

<style scoped>
  .member-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin: 2vh 9vw;
  }

  .member-pics {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }

  .member-pic {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .subteam-info {
    padding: 0 7vw;
    line-height: 2;
    font-size: 1.3rem;
    color: #2c3e50;
  }

  h2 {
    margin: 3px;
    color: #2c3e50;
  }

  @media screen and (max-width: 700px) {
    .member-info {
      flex-direction: column;
    }

    .subteam-info {
      padding: 0 10vw;
      margin-top: 4vh;
    }

    .member-pics {
      flex-direction: column;
    }

    .member-pic {
      transform: scale(.7, 1);
      margin-top: 4vh;
    }
  }

</style>



