<template>
  <div class="nav-bar">
    <div class="nav-control" :class="{open: isClicked}">
      <img src="../assets/logo-transparent.png" alt="logo">

      <div class="nav-toggler" :class="{open: isClicked}" @click='toggle()'>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>


    <nav>
        <router-link :to="{name: 'home'}" 
                      active-class="selected" exact>
                      HOME
        </router-link>

        <router-link :to="{name: 'team'}"
                     active-class="selected">
                     TEAM
        </router-link>

        <router-link :to="{name: 'vehicles'}"
                     active-class="selected">
                     VEHICLES
        </router-link>

        <router-link :to="{name: 'sponsors'}"
                     active-class="selected">
                     SPONSORS
        </router-link>

        <router-link :to="{name: 'contact'}"
                     active-class="selected">
                     CONTACT US
        </router-link>
      </nav>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isClicked: false
      }
    },

    methods: {
      toggle() {
        this.isClicked = ! this.isClicked;
      }
    }
    
    // destroyed: function() {
    //   localStorage.getItem
    // }
    // computed: {
    //     checkPage() {

    //     }
    // },
  }

</script>

<style scoped>
  .nav-bar {
    background-color: #f3c623;
    opacity: .9;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin: 0;
    position: sticky;
    top: 0;
    z-index: 100;
    width: 100%;
    /* display: absolute; */
  }

  nav {
    height: 100%;
    width: 70%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }

  nav a {
    /* aka router-links */
    color: #2c3e50;
    transition-duration: .3s;
    text-align: center;
    font-weight: 700;
    text-decoration: none;
  }

  nav a::before {
      content:'';
      display: block;
      background-color: #2c3e50;
      height: 4px;
      transform: scale(0, 1);
      transition-duration: .3s;
  }

  nav a.selected::before {
      transform: scale(1, 1);
  }

  nav a:hover::before {
      transform: scale(1, 1);
  }

  nav a:hover {
    transform: scale(1.3);
    /* color: red; */
  }

  /* Toggler Animations */
  .nav-toggler {
    display: none;
    width: 60px;
    height: 45px;
    position: relative;
    /* margin: 50px auto; */
    margin: auto 7vw auto 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .5s ease-in-out;
    -moz-transition: .5s ease-in-out;
    -o-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
    cursor: pointer;
  }

  .nav-toggler span {
    display: block;
    position: absolute;
    height: 9px;
    width: 100%;
    background: #2c3e50;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    -moz-transition: .25s ease-in-out;
    -o-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
  }

  .nav-toggler span:nth-child(1) {
    top: 0px;
  }

  .nav-toggler span:nth-child(2) {
    top: 18px;
  }

  .nav-toggler span:nth-child(3) {
    top: 36px;
  }

  .nav-toggler.open span:nth-child(1) {
    top: 18px;
    -webkit-transform: rotate(135deg);
    -moz-transform: rotate(135deg);
    -o-transform: rotate(135deg);
    transform: rotate(135deg);
  }

  .nav-toggler.open span:nth-child(2) {
    opacity: 0;
    left: -60px;
  }

  .nav-toggler.open span:nth-child(3) {
    top: 18px;
    -webkit-transform: rotate(-135deg);
    -moz-transform: rotate(-135deg);
    -o-transform: rotate(-135deg);
    transform: rotate(-135deg);
  }


  @media screen and (max-width: 700px) {
    .nav-bar {
      display: flex;
      flex-direction: column;
      background-color: transparent;
    }

    .nav-control {
      display: flex;
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
      background-color: #f3c623;
    }

    .nav-toggler {
      display: block;
    }

    nav {
      background-color: #f3c623;
      flex-direction: column;
      align-items: center;
      transform-origin: top;
      transform: scale(1,0);
      transition: transform 300ms ease-in-out, opacity 300ms ease-in-out;
      width: 100%;
      /* display: none; */
      opacity: 0; /* Hide the drop-down initially, other wise there will be a 'slide-up' effect at the beginning*/
    }

    .nav-control.open ~ nav {
      transform: scale(1, 1);
      /* display: inline-block; */
      opacity: 1;
    }

    a {
      margin-bottom: 7px;
      opacity: 0;
      transition: transform 300ms ease-in-out, opacity 300ms ease-in-out;
    }

    .nav-control.open ~ nav a {
      opacity: 1;
    }

  }
</style>



