<template>
  <div class="member-info">
    <div class="member-pic">
      <img src="../../../assets/team/members/chassis-lead.jpg" alt="">
      <h2>Andrew Zhang</h2>
      <h2>Subteam Lead</h2>
    </div>

    <p class="subteam-info">
      The Chassis team is responsible for vehicle structures. The team deals with vehicle form, crash safety, component
      placement, and validates designs using complex FEA to simulate a variety of vehicle conditions and crashes.
      Understanding of static mechanics is a necessity while minimizing vehicle weight, profile/size, and finding the
      critical balance between ergonomic efficiency and structural triangulation.
    </p>
  </div>
</template>

<script>
  export default {

  }

</script>

<style scoped>
  .member-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin: 2vh 9vw;
  }

  .member-pic {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .subteam-info {
    padding: 0 7vw;
    line-height: 2;
    font-size: 1.3rem;
    color: #2c3e50;
  }

  h2 {
    margin: 3px;
    color: #2c3e50;
  }

  @media screen and (max-width: 700px) {
    .member-info {
      flex-direction: column;
    }

    .subteam-info {
      padding: 0 10vw;
      margin-top: 4vh;
    }
  }

</style>



