<template>
  <div class="member-info">
    <div class="member-pic">
      <img src="../../../assets/team/members/software-lead.jpg" alt="">
      <h2>David Widjaja</h2>
      <h2>Subteam Lead</h2>
    </div>

    <p class="subteam-info">
      Working closely with all other sub-teams, the software sub-team works on the vehicle’s primary controllers. The
      team works on transmission networks between the car, the driver, and the on-ground team to monitor the vehicle and
      strategize. Software members work with the controller area network (CAN) which creates a communication web between
      the motor controller, the battery management system, and the dashboard. Projects include embedded firmware design
      for driver control, telemetry interfaces and tools, and simulation software for reaching peak efficiency during
      every leg of the race.
    </p>

  </div>
</template>

<script>
  export default {

  }

</script>

<style scoped>
  .member-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin: 2vh 9vw;
  }

  .member-pic {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .subteam-info {
    padding: 0 7vw;
    line-height: 2;
    font-size: 1.3rem;
    color: #2c3e50;
  }

  h2 {
    margin: 3px;
    color: #2c3e50;
  }

  @media screen and (max-width: 700px) {
    .member-info {
      flex-direction: column;
    }

    .subteam-info {
      padding: 0 10vw;
      margin-top: 4vh;
    }
  }

</style>



