<template>
  <div class="member-info">
    <div class="member-pic">
      <img src="../../../assets/team/members/bms-lead.jpg" alt="">
      <h2>Sanjana Chawla</h2>
      <h2>Subteam Lead</h2>
    </div>

    <p class="subteam-info">
      The Battery Management Systems (BMS) team focuses on the battery control systems and electronics to ensure the
      vehicle’s battery maintains safe operating conditions. The team also works on supplemental circuitry in the form
      of relays, switches, and power distributors. The team works with electrical diagrams, PCB design, and software to
      make a safe and reliable operating system. Tasks include a span of electrical and firmware design projects, while
      making key decisions about the feasible performance, safety, and structure of the vehicle’s battery.
    </p>
  </div>
</template>

<script>
  export default {

  }

</script>

<style scoped>
  .member-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin: 2vh 9vw;
  }

  .member-pic {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .subteam-info {
    padding: 0 7vw;
    line-height: 2;
    font-size: 1.3rem;
    color: #2c3e50;
  }

  h2 {
    margin: 3px;
    color: #2c3e50;
  }

  @media screen and (max-width: 700px) {
    .member-info {
      flex-direction: column;
    }

    .subteam-info {
      padding: 0 10vw;
      margin-top: 4vh;
    }
  }

</style>



