<template>
  <div class="team-container">
    <!-- <h1>Team Page</h1> -->

    <img src="../../assets/team/whole-team.jpg" class="team-photo">

    <p class="intro">
      UBC Solar provides an opportunity for undergraduate students to transcend the classroom and learn in a highly
      interdisciplinary, team environment. Our goal is to foster the technical skills of our members within a
      project-based framework in order to develop applications in the sustainable technology sector. We consist of
      roughly 80 students, all of whom volunteer their time outside of academic studies. The team is subdivided into
      three divisions:
    </p>

    <div class="divisons">
      <router-link :to="{name: 'mechanical'}" tag="button" active-class="selected">
        <img src="../../assets/team/mech-icon.png" class="icon">
        Mechanical</router-link>
      <router-link :to="{name: 'electrical'}" tag="button" active-class="selected">
        <img src="../../assets/team/elec-icon.png" class="icon">
        Electrical</router-link>
      <router-link :to="{name: 'management'}" tag="button" active-class="selected">
        <img src="../../assets/team/manage-icon.png" class="icon">
        Management</router-link>
    </div>


    <transition mode="out-in" enter-active-class=" animate__animated animate__fadeIn animate__faster"
      leave-active-class=" animate__animated animate__fadeOut animate__faster ">
      <router-view></router-view>
    </transition>

  </div>
</template>

<script>
  export default {
    created: function () {
      document.querySelector('#app').style.height = '200%';
    }
  }

</script>

<style scoped>
  .team-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    align-items: center;
  }

  .team-photo {
    width: 100%;
  }

  .divisons {
    display: flex;
    flex-direction: row;
    margin-bottom: 5vh;
  }

  .intro {
    color: #2c3e50;
    padding: 0 7vw;
    font-size: 1.5rem;
    line-height: 2;
    margin: 20vh 0;
    font-weight: 700;
  }

  .btns {
    display: flex;
    flex-direction: row;
    margin-bottom: 8vh;
  }

  button {
    font-family: 'Ubuntu', sans-serif;
    color: #2c3e50;
    font-weight: 700;
    font-size: 1rem;
    margin: 3vh 6vw;
    border: 0;
    outline: none;
    width: 170px;
    height: 50px;
    border-radius: 10px;
    background-color: #f3c623;
    cursor: pointer;
    transition-duration: .3s;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }

  .icon {
    height: 40px;
    width: 40px;
  }

  button:hover,
  .selected {
    transform: scale(1.1);
  }

  @media screen and (max-width: 700px) {

    .divisons {
      flex-direction: column;
    }
  }

</style>



