<template>
  <div class="member-info">
    <div class="member-pic">
      <img src="../../../assets/team/members/power-elec-lead.jpg" alt="">
      <h2>Isaiah Adejumo</h2>
      <h2>Subteam Lead</h2>
    </div>

    <p class="subteam-info">
      Power electronics is a dynamic sub-team that works with the motor, motor controller, solar array, and maximum
      power point trackers (MPPTs) to ensure that the car can drive continuously on nothing but solar power. The team
      focuses on configuration and testing HV and LV systems, making use of their electronics knowledge and skills to
      optimize the performance of the car. The team ensures that the true power of solar technology is optimized to the
      fullest. Projects include a combination of electrical testing and mechanical integration, while also taking on
      specialty projects like thermal management and solar panel encapsulation.
    </p>
  </div>
</template>

<script>
  export default {

  }

</script>

<style scoped>
  .member-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin: 2vh 9vw;
  }

  .member-pic {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .subteam-info {
    padding: 0 7vw;
    line-height: 2;
    font-size: 1.3rem;
    color: #2c3e50;
  }

  h2 {
    margin: 3px;
    color: #2c3e50;
  }

  @media screen and (max-width: 700px) {
    .member-info {
      flex-direction: column;
    }

    .subteam-info {
      padding: 0 10vw;
      margin-top: 4vh;
    }
  }

</style>



